nav.navbar {
  margin-bottom: 0;
  background-color: green !important ;
}
nav.navbuttons {
  float: right;
  
}
.thead-dark {
    
    background-color: green !important;

}

.table {
    width: 90vw !important;
    
}
.jumbotron {
  /* min-height: 300px; */
  padding-top:21.25%;
  background-image: url(https://irp-cdn.multiscreensite.com/520e9c30/dms3rep/multi/Depositphotos_199511230_xl-2015RESIZED.jpg);
  background-size:cover;
}

.title {

  background: none;
  top: -150px;
  border: none;

}

h1 {
 
  color:floralwhite;
  font-family: 'PT Serif', serif;
  /* font-weight: bold; */
  font-size: 3.5rem;
  text-shadow: -2px 0 black, 0 2px black, 2px 0 black, 0 -2px black;
  margin-bottom: 40px;

}

.btn {
  color: white;
  background-color: green;
}

.form-control {
  background-color: rgba(255, 255, 255, 0.5);
  background-blend-mode: soft-light;
  color: black !important;
}
.form-control::-webkit-input-placeholder {
  color: black;
}
.form-control::-moz-placeholder {
  color: black;
}
.form-control:-ms-input-placeholder {
  color: black;
}
.form-control::-ms-input-placeholder {
  color: black;
}
.form-control::placeholder {
  color: black;
}

body{margin-top:20px;
    background:#eee;
    }
    
    .panel-teal .panel-heading {
        background-color: #37BC9B;
        border: 1px solid #36b898;
        color: white;
    }
    
    .panel .panel-heading {
        padding: 5px;
        border-top-right-radius: 3px;
        border-top-left-radius: 3px;
        border-bottom: 1px solid #DDD;
        border-radius: 0px;
    }
    
    .panel .panel-heading .panel-title {
        padding: 10px;
        font-size: 17px;
    }
    
    form .form-group {
        position: relative;
        margin-left: 0px !important;
        margin-right: 0px !important;
    }
    
    .inner-all {
        padding: 10px;
    }
    
    .btn.btn-social{
      color: white;
      opacity:0.9;
    }
    .btn.btn-social:hover {
      color: white;
        opacity:1;
    }
    .btn.btn-facebook {
    background-color: #3b5998;
    opacity:0.9;
    }
    .btn.btn-twitter {
    background-color: #00aced;
    opacity:0.9;
    }
    .btn.btn-linkedin {
    background-color:#0e76a8;
    opacity:0.9;
    }
    .btn.btn-github{
      background-color:#000000;
      opacity:0.9;
    }
    .btn.btn-google {
      background-color: #c32f10;
      opacity: 0.9;
    }
    .btn.btn {
      background-color: gray;
    }
    
   
    
    
    .bs-callout {
        background: white;
        border-color: #eee;
        -webkit-border-image: none;
                border-image: none;
        border-radius: 3px;
        border-style: solid;
        border-width: 1px 1px 1px 5px;
        margin-bottom: 5px;
        padding: 20px;
    }
    .bs-callout:last-child {
        margin-bottom: 0px;
    }
    .bs-callout h4 {
        margin-bottom: 10px;
        margin-top: 0;
    }
    
    .bs-callout-danger {
        border-left-color: green;
    }
    
    .bs-callout-danger h4{
        color: green;
    }
    .image{
     max-width: 170px;
    max-height: 170px;
    }
    
    
    
    
    
    @media (max-width: 992px) {
      .resume-heading .social-btn-holder{
        padding:5px;
      }
    }
    
